body {
  background-color: rgb(51, 51, 51);
}
.app-wrapper {
  display: grid;
  width: 1200px;
  margin: 0 auto;
  grid-template-areas: 
  'h h'
  'n c';
  grid-template-rows: 60px 1fr;
  grid-template-columns: 2fr 10fr;
  gap: 10px;
}

.app-wrapper-content {
  grid-area: c;
  background-color: cornflowerblue;

  border-radius: 9px;
  padding: 10px;
}